import DeepData from "../../../assets/js/formate.js";
export default {
  data() {
    return {
      orderlist: ["ascending", "descending"],
      tableData: [],
      form: {
        info: "",
        num_times: "",
        circulation: "",
        play_start_time: "",
        play_end_time: ""
      },
      rules: {
        info: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        num_times: [
          {
            required: true,
            message: "必填项不可以为空,需要填入数字",
            trigger: "blur"
          }
        ],
        circulation: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        play_start_time: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        play_end_time: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ]
      },
      total: 0,
      currentPage: 1,
      limit: 10,
      visible: false,
      title: "添加系统公告",
      selectList: [],
      loading: false
    };
  },
  created() {
    this.initdata({ page: this.currentPage, limit: this.limit });
  },
  methods: {
    //批量删除
    async del() {
      //勾选需要删除的项目批量删除
      if (this.selectList.length != 0) {
        this.$confirm("确认删除吗?", "信息", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(async () => {
            //###1.删除dom的数据
            let str = this.selectList.join();
            // console.log(str);
            let { data } = await this.$http.HallFunConfig.DeleteSysBroadcast({
              id_list: `(${str})`
            });
            console.log(data);
            if (data.code === 1 && data.msg === "ok") {
              this.initdata({ page: this.currentPage, limit: this.limit });
            }

            this.$message({
              type: "success",
              message: "删除成功!"
            });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "已取消删除"
            });
          });
      } else {
        this.$message("请选择需要删除的数据");
      }
    },

    //添加
    add(formName) {
      this.editForm("添加系统公告", true, {});

      this.$refs.formName.resetFields();
    },

    //发送给服务器配置
    send() {
      this.$confirm("确认发送吗?", "信息", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      }).then(async () => {
        this.loading = true;
        let { data } = await this.$http.HallFunConfig.PostTableConfig({
          type_id: 6
        });
        // console.log(data);
        if (data.code === 1 && data.data ) {
          this.loading = false;
          this.$message({
            type: "success",
            message: data.msg
          });
        }

       
      });
    },

    //表格多选
    handleSelectionChange(sel) {
      let idList = sel.map(item => item.id);
      // console.log(idList);

      this.selectList = idList;
    },

    //页容量发生变化
    handleSizeChange(num) {
      this.limit = num;
      this.currentPage = 1;
      this.initdata({ page: this.currentPage, limit: this.limit });
    },

    //页码发生变化
    handleCurrentChange(pagenum) {
      this.currentPage = pagenum;
      this.initdata({ page: this.currentPage, limit: this.limit });
    },

    //表格编辑
    handleEdit(row, formName) {
      let rowform = DeepData(row);
      // console.log(rowform);
      rowform.play_start_time = this.data(row.play_start_time);
      rowform.play_end_time = this.data(row.play_end_time);
      // console.log(rowform);
      this.editForm("更新系统公告", true, rowform);

      this.$refs.formName.resetFields();
    },

    //表格删除
    handleDelete(x, row) {
      // console.log(x, row.id);

      this.$confirm("确认删除吗?", "信息", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(async () => {
          let { data } = await this.$http.HallFunConfig.DeleteSysBroadcast({
            id: row.id
          });
          // console.log(data);
          if (data.code === 1 && data.msg === "ok") {
            this.initdata({
              page: this.currentPage,
              limit: this.limit,
              title: this.searchinput
            });
          }
          this.$message({
            type: "success",
            message: "删除成功!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },

    //表单提交
    onSubmit(formName, type) {
      //表单验证
      this.$refs[formName].validate(async valid => {
        if (valid) {
          if (type === "添加系统公告") {
            // console.log(this.form);
            let { data } = await this.$http.HallFunConfig.PostSysBroadcast(
              this.form
            );
            if (data.code === 1 && data.msg === "ok") {
              this.initdata({ page: this.currentPage, limit: this.limit });
            }
            // console.log(data);

            // this.initdata({ page: this.currentPage, limit: this.limit })
          } else if (type === "更新系统公告") {
            let { data } = await this.$http.HallFunConfig.PutSysBroadcast(
              this.form
            );
            // console.log(data);
            if (data.code === 1 && data.msg === "ok") {
              this.initdata({ page: this.currentPage, limit: this.limit });
            }
          }
          this.editForm("添加系统公告", false, {});
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },

    //表单返回
    back() {
      this.editForm("添加系统公告", false, {});
    },

    editForm(title, visible, form) {
      this.title = title;
      this.visible = visible;
      this.form = form;
    },

    formateData(res) {
      res.forEach(item => {
        item.circulation = item.circulation === 1 ? "按时间" : "按日期时间";
      });
      return res;
    },

    data(time) {
      let long1 = Date.parse(time);
      let long2 = new Date(long1).getTime();
      return long2;
    },

    async initdata(params) {
      let { data } = await this.$http.HallFunConfig.GetSysBroadcast(params);
      // let deepData = DeepData(data.data)
      let localdata = this.formateData(DeepData(data.data));
      this.tableData = localdata;
      this.total = data.total;
      // console.log(localdata);
      // console.log(data);
    }
  }
};



